// footer 
footer{
    background-image: url('../img/footer_bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
    position: relative;
    
    .footer__content{
        transform: translateY(-20%);
        margin-bottom: -110px;
        background: #fff;
        position: relative;
        overflow: hidden;
        padding: 83px 80px 106px 80px ;
        border-radius: 50px;
        box-shadow: 0 0 30px rgba(0,0,0,0.35);
        h2{
            font-size: 36px;
            font-weight: 600;
            margin-bottom: 10px;
        }
        p.naming{
            font-weight: bold;
        }
        p{
            color: $color_blue;
            // font-weight: bold;
            margin-top: 0;
             font-size: 18px;
        }
        .row{
            span{
                font-size: 19px;
                margin-top: 30px;
            }
            p.tel{
                a{
                    color: #000;
                    font-size: 19px;
                    @include font-bold;
                    margin-bottom: 30px;
                    margin-top: 30px;
                }
            }
            p.adress{
                color: #000;
                    font-size: 19px;
                    @include font-bold;
                    
            }
            p{
                color: #000;
                font-size: 18px;
            }
            h1{
                font-size: 36px;
                font-weight: 600;
                span{
                    color: $color_blue;
                    font-size: 36px;
                    margin-top: 0;
                }
            }
            .form__head{
                padding-left: 30px;
            }
            form{
                margin-top: 80px;
                padding-left: 30px;
                input{
                    width: 100%;
                    max-width: 415px;
                    border: 3px solid #E3E5EB;
                    border-radius: 94px;
                    height: 64px;
                    padding-left: 82px;
                    @include font-bold;
                    font-size: 19px;
                    // outline: 3px solid $color_blue;
                    outline: none;
                    @include transition;

                }
                input::-webkit-input-placeholder { color: #393939; }
                input:hover{
                    border: 3px solid $color_yellow;
                }
                input:focus{
                    border: 3px solid $color_blue;
                }
                .input_container{
                    // display: inline-block;
                    position: relative;
                    margin-bottom: 18px;
                }
                .input_container::after{
                    content: '';
                    width: 34px;
                    height: 34px;
                    // background-image: url('../img/person.png');
                    background-repeat: no-repeat;
                    background-size: cover; 
                    position: absolute;
                    top: 50%;
                    left: 30px;
                    transform: translateY(-50%);                 
                }
                .input_container.name::after{
                    background-image: url('../img/person.png');
                }
                .input_container.phone::after{
                    background-image: url('../img/phone.png');
                }
                .input_container.mail::after{
                    background-image: url('../img/mail.png');
                }
                .btn{
                    width: 100%;
                    max-width: 415px;
                    color: #fff;
                    margin-bottom: 55px;
                }
                .btn:hover{
                    color: #000;
                }
            }
        }
        .show_policy{
            cursor: pointer;
        }
        .contact_img{
            position: absolute;
            bottom: 0;
            left: 0;
        }
    }
    .footer__info{
        display: flex;
        justify-content: center;
        flex-direction: column-reverse;
        text-align: center;
        color: #fff;
        p{
            font-size: 25px;
        }
        .social{
            // margin-bottom: 15px;
            span{
                font-size: 30px;
            }
            span:before{
                color: #fff;
            }
            a{
                text-decoration: none;
            }
            a:not(:last-child){
                margin-right: 37px;
            }
        }
        
    }
    .pat{
        position: absolute;
        top: -30%;
        right: 38%;
    }
    @media screen and ( max-width:1600px){
        .pat{
            right: 34%;
        }
    }
    @media screen and ( max-width:1440px){
        .footer__info{
            .social{
                margin-bottom: 30px;
            }
        }
        .footer__content{
            padding: 50px 60px 50px 60px;

            h2{
                font-size: 27px;
            }
            .row{
                span{
                    font-size: 15px;
                }
                p.tel{
                    a{
                        font-size: 15px;
                    }
                }
                p.adress{
                    font-size: 15px;
                    
                }
                p{
                    font-size: 18px;
                }
                h1{
                    font-size: 27px;
                    span{
                        font-size: 27px;
                    }
                }
                form{
                    margin-top: 60px;
                    input{
                        height: 54px;
                        font-size: 15px;
                        padding-left: 60px ;
                    }
                    .input_container::after{
                        width: 25px;
                        height: 25px;
                        left: 25px;
                    }
                }
            }
            .contact_img{
                max-width: 375px ;
            }
        }
        .pat{
            max-width: 80px;
            top: -27.5%;
        }
    }
    @media screen and ( max-width:1024px){
        .footer__content{
            .row{
                h1{
                    font-size: 27px;
                    span{
                        font-size: 27px;
                    }
                }
            }
            .contact_img{
                width: 400px;
            }
        }
    }
    @media screen and ( max-width:768px){
        background-image: none;
        .footer__content{
            margin-left: -15px;
            margin-right: -15px;
            transform: translateY(0);
            border-radius: 0;
            overflow: visible;
            // box-shadow: none;
            padding: 70px 15px 50px 15px;
            .shadow-box{
                box-shadow: 0px 40px 30px -32px rgba(0,0,0,0.35);
            }
            .row{
                span{
                    font-size: 22px;
                }
                .col-md-6{
                    padding: 40px 15px;
                }
                flex-wrap: wrap-reverse;
                .form__head{
                    display: flex;
                    justify-content: center;
                    padding-left: 0;
                }
                h1{
                    display: inline-block;
                    font-size: 24px;
                    span{
                        font-size: 24px;
                    }
                }
                form{
                    padding-left: 0;
                    margin-top: 32px;
                    input{
                        height: 56px;
                        font-size: 18px;
                    }
                    .input_container{
                        margin-bottom: 17px;
                    }
                }
                p{
                    font-size: 18px;
                }
           
            }
            h2.mobile{
                font-size: 24px;
            }
            p.mobile{
                font-size: 18px;
                color: $color_blue;
                font-weight: 600;
                margin-bottom: 12px;
            }
            .contact_social.mobile{
                margin-top: 30px;
                margin-right: 15px;
                display: flex !important;
                align-items: center;
                justify-content: space-between;
                img{
                    position: relative;
                    left: -15px;
                    width: 310px;
                }
                .social{
                    text-align: center;
                    // display: inline-block;
                    a{
                        display: inline-block;
                        margin-bottom: 20px;
                        text-decoration: none;
                    }
                    span::before{
                        color: #000;
                        @include transition
                    }
                    span:hover:before{
                        color: $color_blue;
                    }
                }
            }
        }
        .footer__info{
            justify-content: center;
            p{
                color: #000;
                font-size: 18px;
                position: relative;
                text-align: center;
                font-weight: 600;
            }
        }
        .pat{
            width: 65px;
            top: -25px;
            right: 42%;
        }
        
    }
    @media screen and ( max-width: 320px){
        background-image: none;
        .footer__content{
            margin-left: -15px;
            margin-right: -15px;
            transform: translateY(0);
            border-radius: 0;
            overflow: visible;
            // box-shadow: none;
            padding: 100px 15px 50px 15px;
            .shadow-box{
                box-shadow: 0px 40px 30px -32px rgba(0,0,0,0.35);
            }
            .row{
                span{
                    font-size: 18px;
                }
                .col-md-6{
                    padding: 40px 15px;
                }
                flex-wrap: wrap-reverse;
                .form__head{
                    display: flex;
                    justify-content: center;
                    padding-left: 0;
                }
                h1{
                    display: inline-block;
                    font-size: 18px;
                    span{
                        font-size: 20px;
                    }
                }
                form{
                    padding-left: 0;
                    margin-top: 20px;
                    input{
                        height: 50px;
                        font-size: 16px;
                    }
                    .input_container{
                        margin-bottom: 10px;
                    }
                }
                p{
                    font-size: 14px;
                }
           
            }
            h2.mobile{
                font-size: 18px;
            }
            p.mobile{
                font-size: 14px;
                color: $color_blue;
                font-weight: 600;
                margin-bottom: 12px;
            }
            .contact_social.mobile{
                margin-top: 30px;
                margin-right: 15px;
                display: flex !important;
                align-items: center;
                justify-content: space-between;
                img{
                    position: relative;
                    left: -15px;
                    width: 250px;
                }
                .social{
                    text-align: center;
                    // display: inline-block;
                    a{
                        display: inline-block;
                        margin-bottom: 20px;
                        text-decoration: none;
                    }
                    span::before{
                        color: #000;
                        @include transition
                    }
                    span:hover:before{
                        color: $color_blue;
                    }
                }
            }
        }
        .footer__info{
            justify-content: center;
            p{
                color: #000;
                font-size: 14px;
                position: relative;
                text-align: center;
                font-weight: 600;
            }
        }
        .pat{
            width: 65px;
            top: -25px;
            right: 42%;
        }
        
    }
}
@import 'footer_page';
// footer 
@media (max-width: 320px){
    .menu_right{
        display: none !important;
    }
}