main.page{
    background: #F7F7F7;
    // step 
    section.step{
        padding-top: 35px;
        position: relative;
        .pat{
            position: absolute;
            top: 150px;
            transform: rotate(90deg);
            left: 5%;
        }
        .container_steps{
            margin-left: 20%;
            overflow: hidden;
            padding-top: 40px;
            padding-left: 20px;

            h1{
                margin-bottom: 110px;
                font-size: 36px;
                font-weight: 600;
            }
            .swiper-container{
                overflow: visible;
                .slider_navigation{
                    margin-top: 34px;
                }
                .swiper-slide{
                    display: inline-block;
                    width: auto;
                    height: 350px;
                    max-width: 270px;
                    padding: 30px 42px;
                    position: relative;
                    background: #fff;
                    border-radius: 30px;
                    box-shadow: 0 3px 10px rgba(0,0,0,0.35);
                    @include transition;
                    font-size: 18px;
                    h1{
                        margin-bottom: 20px;
                        font-size: 17px;
                    }

                    .counter{
                        position: absolute;
                        left: 40px;
                        top: 0;
                        transform: translateY(-60%);
                        font-size: 60px;
                        color: $color_blue;
                        
                        @include transition;
                    }
                    
                }
                .slider_navigation{
                    position: relative;
                    left: -12%;
                    width: 100%;
                    justify-content: center;
                }
                .swiper-slide.fix{
                    font-size: 16px;
                    max-width: 300px;
                }
                .swiper-slide:hover{
                    box-shadow: 0 3px 10px rgba(0,0,0,0.6);
                    .counter{
                        color: $color_yellow;
                    }
                }
            }
        }
        @media screen and(max-width:1440px){
            .pat{
                max-width: 190px ;
            }
        }
        @media screen and(max-width:1024px){
            padding-top: 50px;
            .pat{
                display: none;
            }
            .container_steps{
                margin-left: 3%;
                .swiper-container{
                    .swiper-slide{
                        font-size: 14px;
                        max-width: 220px;
                    }
                    .slider_navigation{
                        left: -2%;
                    }
                }
            }
        }
        @media screen and(max-width:768px){
            padding-top: 0;
            .container_steps{
                 h1{
                     margin-bottom: 60px;
                     font-size: 20px;
                 }
                 .swiper-container{
                     .swiper-slide{
                         height: 270px;
                     }
                 }
            }
        }
    }
    // step 

    //need doc
    section.need_document{
        padding-top: 100px;
        padding-bottom: 100px;
        position: relative;
        .need_document__container{
            border-radius: 50px;
            background: #fff;
            position: relative;
            overflow: hidden;
            display: flex;
            align-items: center;
            position: relative;
            z-index: 10;
            box-shadow: 0 0 30px rgba(0,0,0,0.35);
            .need_document__content{
                margin-left: 40px;
                h2{
                    font-size: 36px;
                    font-weight: 600;
                }
                .list{
                    margin-top: 60px;
                    .li{
                        display: flex;
                        align-items: center;
                        margin-bottom: 20px;

                        span{
                            display: flex;
                            width: 50px;
                            height: 50px;
                            justify-content: center;
                            align-items: center;
                            border: 3px solid $color_yellow;
                            border-radius: 50%;
                            margin-right: 20px;
                        }
                        P{
                            margin: 0;
                            font-size: 20px;
                        }
                    }
                }
                

            }
            
        }
        .pat{
            position: absolute;
            z-index: 9;
        }
        .pat_1{
            right: 8%;
            top: 0px;
        }
        .pat_2{
            left: 16%;
            top: 60%;
        }
        @media screen and(max-width:1600px){
            .pat_1{
                right: -2%;
                top: -20px;
            }
            .pat_2{
                left: 6%;
                top: 70%;
            }
        }
        @media screen and(max-width:1600px){
            .need_document__container{
                img{
                    max-width: 378px;
                }
                .need_document__content{
                    h2{
                        font-size: 27px;
                    }
                    .list{
                        margin-top: 45px;
                        .li{
                            p{
                                font-size: 18px;
                            }
                            span{
                                width: 38px;
                                height: 38px;
                            }
                            
                        }
                    }
                }
            }
            .pat_1{
                max-width: 268px ;
                right: 8%;
            }
            .pat_2{
                max-width: 136px;
                top: 65%;
                left: 12%;
            }
        }
        @media screen and(max-width:1024px){
            .need_document__container{
                img{
                    max-width: 400px;
                }
                .need_document__content{
                    margin-left: 0;
                    h2{
                        font-size: 26px;
                    }
                    .list{
                        .li{
                            p{
                                font-size: 18px;
                            }
                            span{
                                width: 36px;
                                min-width: 36px;
                                height: 36px;
                            }
                        }
                    }
                }
            }
        }
        @media screen and(max-width:768px){
            padding-top: 40px;
            padding-bottom: 42px;
            .need_document__container{
                border-radius: 0;
                box-shadow:none;
                display: block;
                overflow: visible;
                img{
                    position: relative;
                    left: -15px;
                }
                .need_document__content{
                    
                    h2{
                        font-size: 18px;
                        margin-top: 50px;
                    }
                    .list{
                        margin-top: 18px;
                        .li{
                            p{
                                font-size: 14px;
                            }
                        }
                    }
                }
            }
            .pat{
                display: none;
            }
        }
    }
    //need doc

    // timing 
        section.timing{
            position: relative;
            padding-bottom: 100px;
            .centered{
                display: flex;
                align-items: center;
            }
            .time{
                display: flex;
                align-items: center;
                position: relative;
                z-index: 10;
                span{
                    font-size: 90px;
                    margin-right: 40px;
                }
                .info{
                    h2{
                        font-size: 36px;
                        font-weight: 600;
                    }
                    p{
                        font-size: 20px;
                        @include font-bold;
                        color: $color_blue;
                    }
                }
            }
            .timing_container{
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                .timing{
                    width: 45%;
                    span.count{
                        font-size: 60px;
                        line-height: 60px;
                        color: $color_blue;
                        position: relative;
                    }
                    span.count::before{
                        content: '';
                        position: absolute;
                        left: 0;
                        bottom: -10px;
                        border-radius: 3px;
                        // transform: translateY(-50%);
                        width: 100%;
                        height: 5px;
                        background: $color_yellow;
                    }
                    p{
                        font-weight: 600;
                        font-size: 18px;
                        margin-bottom: 50px;

                    }
                    p.line{
                        font-size: 18px;
                        padding-left: 80px;
                        position: relative;
                        margin-bottom: 10px;
                        font-weight: normal;
                    }
                    p.line::before{
                        // content: '';
                        // position: absolute;
                        // left: 0;
                        // top: 50%;
                        // transform: translateY(-50%);
                        // width: 60px;
                        // height: 5px;
                        // background: $color_yellow;
                    }
                }
                .timing.one_digit{
                    p.line{
                        padding-left: 50px;
                    }
                }
            }
            .pat{
                position: absolute;
                left: 0;
                top: 40%;
                z-index: 9;
                transform: translateY(-50%);
            }
            @media screen and(max-width:1440px){
                .pat{
                    max-width: 492px;
                }
                .time{
                    span{
                        font-size: 68px;
                    }
                    .info{
                        h2{
                            font-size: 27px;
                        }
                        p{
                            font-size: 15px;
                        }
                    }
                }
                .timing_container{
                    .timing{
                        span.count{
                            font-size: 42px;
                        }
                        span.count::before{
                            bottom: -18px;
                        }
                        p.line{
                            font-size: 18px;
                            padding-left: 60px;
                        }
                        p{
                            font-size: 18px;
                        }
                    }
                }
            }
            @media screen and(max-width:1024px){
                .time{
                    span{
                        font-size: 65px;
                    }
                }
                .timing_container{
                    .timing{
                        span.count{
                            font-size: 42px;
                        }
                        span.count::before{
                            bottom: -18px;
                        }
                        p.line{
                            font-size: 14px;
                            padding-left: 60px;
                        }
                        p{
                            font-size: 14px;
                        }
                    }
                }
                .pat{
                    width: 50%;
                    top: 42%;
                }
            }
            @media screen and(max-width:768px){
                .time{
                    span{
                        font-size: 50px;
                    }
                    .info{
                        h2{
                            font-size: 18px;
                        }
                        p{
                            font-size: 14px;
                            margin: 0;
                        }
                    }
                }
                .timing_container{
                    margin-top: 40px;
                    .timing{
                        width: 100%;
                        p{
                            margin-bottom: 20px;
                        }
                    }
                   
                }
                .pat{
                    display: none;
                }
            }
            @media screen and(max-width:320px){
                .time{
                    span{
                        font-size: 50px;
                    }
                    .info{
                        h2{
                            font-size: 18px;
                        }
                        p{
                            font-size: 14px;
                            margin: 0;
                        }
                    }
                }
                .timing_container{
                    margin-top: 40px;
                    .timing{
                        width: 100%;
                        span.count{
                            font-size: 32px;
                            line-height: 32px;
                            &::before{
                                bottom: -9px;
                            }
                        }
                        p.line{
                            margin-bottom: 0;
                        }
                        p{
                            margin-bottom: 25px;
                        }
                    }
                    .timing.one_digit{
                        p.line{
                            padding-left: 32px;
                        }
                        
                    }
                }
            }
        }
    // timing 

    // dop_timing 
    section.dop_timing{
        background: #fff;
        
        padding-bottom: 245px;
        .container{
            padding-top: 100px;
            position: relative;
        }
        .dop_timing__head{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            text-align: right;
            position: relative;
            
            .pat{
                position: absolute;
                z-index: 9;
                right: 45px;
                top: 50%;
                transform: translateY(-50%) rotate(-90deg);
            }
            span{
                font-size: 90px;
            }
            h2{
                font-size: 36px;
                font-weight: 600;
                margin-right: 40px;
                
            }
            h2, span{
                position: relative;
                z-index: 10;
            }
        }
        .dop_timing__content{
            display: flex;
            margin-top: 80px;
            .timing{
                width: 25%;
                padding-right: 50px;
                span.count{
                    font-size: 48px;
                    color: $color_blue;
                    position: relative;
                }
                span.count::before{
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: -24px;
                    border-radius: 3px;
                    // transform: translateY(-50%);
                    width: 100%;
                    max-width: 70px;
                    height: 5px;
                    background: $color_yellow;
                }
                p{
                    font-weight: 600;
                    font-size: 18px;
                }
                p.line{
                    font-size: 18px;
                    padding-left: 80px;
                    position: relative;
                    font-weight: normal;
                }
                p.line::before{
                    // content: '';
                    // position: absolute;
                    // left: 0;
                    // top: 50%;
                    // transform: translateY(-50%);
                    // width: 60px;
                    // height: 5px;
                    // background: $color_yellow;
                }
            }
            .timing.fix{
                span.count::before{
                    max-width: 100px;
                }
                p.line{
                    padding-left: 120px;
                }
            }
        }
        .pat.pat_2{
            position: absolute;
            top: -110px;
            left: 0;

        }
        @media screen and(max-width:1440px){
            .pat.pat_2{
                max-width: 568px;
            }
            .dop_timing__head{
                h2{
                    font-size: 27px;
                }
                span{
                    font-size: 68px;
                }
                .pat{
                    max-width:186px;
                }
            }
            .dop_timing__content{
                .timing{
                    span.count{
                        font-size: 35px;
                    }
                    p.line{
                        padding-left: 65px;
                    }
                    p{
                        font-size: 18px;
                    }
                }
                .timing.fix{
                    p.line{
                        padding-left: 96px;
                    }
                }
            }
        }
        @media screen and(max-width:1024px){
            .pat.pat_2{
                width: 60%;
                left: -30px;
                top: -60px;
            }
            .dop_timing__content{
                .timing{
                    span.count{
                        font-size: 35px;
                    }
                    p.line{
                        padding-left: 65px;
                    }
                    p{
                        font-size: 14px;
                    }
                }
                .timing.fix{
                    p.line{
                        padding-left: 96px;
                    }
                }
            }
        }
        @media screen and(max-width:768px){
            padding-bottom: 0;
            .dop_timing__head{
                h2{
                    font-size: 18px;
                }
                span{
                    font-size: 50px;
                }
                .pat{
                    // display: none;
                    max-width: 135px;
                    right: 20px;
                }
                
            }
            .dop_timing__content{
                margin-top: 40px;
                flex-wrap: wrap;
                .timing{
                    width: 100%;
                    span.count{
                        line-height: 35px;
                        &:before{
                                bottom: -10px;
                        }
                    }
                    p.line{
                        line-height: 18px;
                        margin-bottom: 0;
                    }
                    p{
                        margin-bottom: 20px;
                    }
                }
            }
            
            .pat.pat_2{
                width: 100%;
            }
        }
    }
    // dop_timing 

    //otzivi
    section.otzivi{
        padding-top: 120px;
        padding-bottom: 230px;
        background: #fff;
        @media screen and(max-width:1024px){
            .pat{
                width: 45%;
                top: 75px;
            }
        }
        @media screen and(max-width:768px){
            padding-top: 60px;
            padding-bottom: 60px;
            .pat{
                display: none;
            }
        }
    }
    //otzivi
    // kviz 
    section.kviz{
        background-image: url('../img/kviz_page_bg.png');
        padding-bottom: 140px;
        position: relative;
        .pat{
            position: absolute;
            top: -23%;
            left: 14%;

        }
        .page_kviz_info{
            text-align: center;
            color: #fff;
            h2{
                font-size: 42px;
                font-weight: 600;
                color: $color_yellow;
                margin-bottom: 34px;
            }
            p{
                font-size: 24px;
                font-weight: normal;
            }
            a{
                font-size: 24px;
                color: #fff;
                text-decoration: none;
                @include font-bold;
            }
        }
        .footer__mobile{
            margin-top: 40px;
            h2{
                font-size: 36px;
                font-weight: 600;
                margin-bottom: 10px;
            }
            p.mobile{
                font-size: 14px;
                color: $color_blue;
                font-weight: 600;
