            }
            .contact_social.mobile{
                margin-top: 30px;
                display: flex !important;
                align-items: center;
                justify-content: space-between;
                img{
                    position: relative;
                    left: -30px;
                }
                .social{
                    text-align: center;
                    // display: inline-block;
                    a{
                        display: inline-block;
