        }
        @media screen and(max-width:1024px){
            .pat{
                display: none;
            }
            .page_kviz_info{
                h2{
                    font-size: 32px;
                }
                p,a{
                    font-size: 18px;
                }
            }
        }
        @media screen and(max-width:768px){
            background: #fff;
            padding-bottom: 60px;
            padding-top: 50px;
        }
    }
    // kviz 
    @media screen and(max-width:768px){
        background: #fff;
    }
}
