// Advantages
section.advantages{
    position: relative;
    background-color: #fcfcfc;
    // overflow: hidden;
    .advantages_container{
        display: flex;
        padding: 75px 50px;
        background: #fff;
        border: 0.877196px solid #E8E9ED;
        box-sizing: border-box;
        box-shadow: 0 85px 40px -100px rgba(0,0,0,0.5);
        border-radius: 20.0246px;
        position: relative;
        z-index: 10;
        top: -80px;
        margin-bottom: -80px;
        .advantage{
            width: 25%;
            text-align: center;
            span{
                font-size: 65px;
                
                // margin-bottom: 30px;
            }
            span::before{
                color: $color_blue;
                @include transition;
            }
            span:hover::before{
                color: $color_yellow;
            }
            p{
                font-size: 16px;
                margin-top: 30px;
                @include font-bold;
            }
        }
    }
    .pat{
        position: absolute;
        bottom: -50px;
        left: 7%;
        z-index: 9;
    }
    @media screen and ( max-width:1600px){
        .advantages_container{
            // top: -50px;
            // width: 80%;
            // margin: 0 auto;
            .advantage{
                span{
                    font-size: 60px;
                }
                p{
                    font-size: 14px;
                }
            }
            
        }
        .pat{
            max-width: 255px;
            bottom: -20px;
            left: 10%;
        }

    }
    @media screen and ( max-width:1440px){
        .advantages_container{
            .advantage{
                span{
                    font-size: 50px;
                }
                p{
                    font-size: 14px;
                }
            }
        }
    }
    @media screen and ( max-width:1024px){
        .advantages_container{
            flex-wrap: wrap;
            width: 100%;
            .advantage{
                span{
                    font-size: 58px;
                }
                p{
                    font-size: 14px;
                }
            }
        }
    }
    @media screen and ( max-width:768px){
        .advantages_container{
            top: -42px;
            padding: 78px 0px;
            .advantage{
                width: 100%;
                span{
                    font-size: 58px;
                }
                p{
                    font-size: 18px;
                    font-weight: 600;
                    font-family: "Futura PT Book";
                }
            }
        }
    }
    @media screen and ( max-width:320px){
        .advantages_container{
            top: -42px;
            padding: 56px 34px;
            .advantage{
                width: 100%;
                span{
                    font-size: 42px;
                }
                p{
                    font-size: 14px;
                }
            }
        }
    }
}
