    // kviz 
    section.kviz{
        background-image: url('../img/footer_bg.png');
        background-size: cover ;
        background-repeat: no-repeat;
        position: relative;
        .kviz__content{
            position: relative;
            z-index: 10;
            min-height: 880px;
            padding: 120px 100px 140px 100px;
            background: #fff;
            border-radius: 50px;
            overflow               : hidden;
            box-shadow: 0 0 30px rgba(0,0,0,0.35);
            

            h3{
                font-size: 36px;
                font-weight: 600;
                span{
                    color: $color_blue;
                }
            }
            form{
                position: relative;
                font-size: 19px;

                label{
                    padding-top: 9px;
                    margin-right: 30px;
                    margin-top: 30px;
                }
                // input[type="radio"]:checked, 
                // input[type="radio"]:not(:checked){
                //     margin-top: 30px;
                // }
                input[type="checkbox"]:checked, 
                input[type="checkbox"]:not(:checked), 
                input[type="radio"]:checked, 
                input[type="radio"]:not(:checked) 
                {
                    position: absolute;
                    left: -9999px;
                }

                input[type="checkbox"]:checked + label, 
                input[type="checkbox"]:not(:checked) + label, 
                input[type="radio"]:checked + label, 
                input[type="radio"]:not(:checked) + label {
                    display: inline-block;
                    position: relative;
                    padding-left: 58px;
                    line-height: 20px;
                    cursor: pointer;
                }
                input[type="checkbox"]:checked + label:before, 
                input[type="checkbox"]:not(:checked) + label:before,
                input[type="radio"]:checked + label:before, 
                input[type="radio"]:not(:checked) + label:before {
                    content: "";
                    position: absolute;
                    left: 0px;
                    top: 0px;
                    width: 40px;
                    height: 40px;
                    border: 1px solid $color_blue;
                    background-color: #ffffff;
                }

                input[type="checkbox"]:checked + label:before, 
                input[type="checkbox"]:not(:checked) + label:before {
                    border-radius: 2px;
                }

                input[type="radio"]:checked + label:before, 
                input[type="radio"]:not(:checked) + label:before {
                    border-radius: 5px;
                }
                input[type="checkbox"]:checked + label:after, 
                input[type="checkbox"]:not(:checked) + label:after, 
                input[type="radio"]:checked + label:after, 
                input[type="radio"]:not(:checked) + label:after {
                    content: "";
                    position: absolute;
                    -webkit-transition: all 0.2s ease;
                    -moz-transition: all 0.2s ease;
                    -o-transition: all 0.2s ease;
                    transition: all 0.2s ease;
                }

                input[type="checkbox"]:checked + label:after, 
                input[type="checkbox"]:not(:checked) + label:after {
                    left: 12px;
                    top: 12px;
                    width: 18px;
                    height: 10px;
                    border-radius: 1px;
                    border-left: 4px solid $color_blue;
                    border-bottom: 4px solid $color_blue;
                    -webkit-transform: rotate(-45deg);
                    -moz-transform: rotate(-45deg);
                    -o-transform: rotate(-45deg);
                    -ms-transform: rotate(-45deg);
                    transform: rotate(-45deg);
                }

                input[type="radio"]:checked + label:after, 
                input[type="radio"]:not(:checked) + label:after {
                    left: 8px;
                    top: 8px;
                    width: 25px;
                    height: 25px;
                    border-radius: 100%;
                    background-color: $color_blue;
                }
                input[type="checkbox"]:not(:checked) + label:after, 
                input[type="radio"]:not(:checked) + label:after {
                    opacity: 0;
                }

                input[type="checkbox"]:checked + label:after, 
                input[type="radio"]:checked + label:after {
                    opacity: 1;
                }

                .btn_container{
                    text-align: right;
                    margin-top: 60px;
                    .btn{
                        padding: 15px 40px;
                        border-radius: 30px;
                        border: 3px solid $color_yellow;
                        font-size: 19px;
                        @include font-bold;
                        @include transition;
                    }
                    .btn.next:hover,
                    .btn.prev:hover{
                        background: $color_yellow;
                    }
                    .btn.next:active,
                    .btn.prev:active{
                        background:$color_yellow_pressed;
                        border: 3px solid $color_yellow_pressed;
                    }
                }
            }
            .counter{
                display: flex;
                align-items: center;
                justify-content: center;
                span.cur{
                    position: relative;
                    z-index: 10;
                    font-size: 100px;
                    font-weight: 600;
                    color: $color_blue;
                    span.icon-msg{
                        position: absolute;
                        top: 0;
                        left: -10px;
                        z-index: -1;
