            }
            .mobile_fix:after{
                content: '';
                width: 100%;
                height: 60px;
                background:  linear-gradient( to top, rgba(255,255,255,1), rgba(255,255,255,0) );
                position: absolute;
                bottom: 0;
                left: 0;
            }
        }
        @media screen and ( max-width:320px){
            padding-top: 50px;
            padding-bottom: 60px;
            h1{
                font-size: 56px;
                margin-bottom: 12px;
            }
            .fix-p{
                padding: 0 15px ;
            }
            .swiper-slide{
                h2{
                    font-size: 24px;
                    margin-bottom: 15px;
                }
                .col-md-6{
                    h2{
                        font-size: 14px;
                        margin-top: 10px;
                    }
                    p{
                        font-size: 14px;
                    }
                }
                .case__info{
                    span{
                        font-size: 12px;

                    }
                    p{
                        font-size: 12px;
                    }
                }
            }
            .slick-slider{
                width: 85%;
                margin: 0 auto;
            }
            .mobile_fix{
                max-height: 200px;
                overflow: hidden;
                position: relative;
                padding-bottom: 60px;
                @include transition;

                button.more{
                    position: absolute;
                    z-index: 100;
                    bottom: 0px;
                    right:10px;
                    border: none;
                    background: none;
                    font-size: 14px;
                    color: #000;
                    outline: none;
                }
                
            }
            .mobile_fix.show_more{
                max-height: unset;
                overflow: visible;
            }
            .mobile_fix:after{
                content: '';
                width: 100%;
                height: 60px;
                background:  linear-gradient( to top, rgba(255,255,255,1), rgba(255,255,255,0) );
                position: absolute;
                bottom: 0;
                left: 0;
            }
        }
    }
