$color_yellow: #F5D061;
$color_yellow_pressed: #DBBB5C;
$color_blue: #3E84D7;
@mixin transition {transition:.5s all ease}
@mixin transition_fade{
    opacity: 0;
    visibility: hidden;
    @include transition;
}
@mixin transition_show{
    opacity: 1;
    visibility: visible;
}
@mixin font-bold{
    font-family: 'Futura PT';
    font-weight: bold;
    font-style: normal;
}
@mixin font-light{
    font-family: 'Futura PT';
    font-weight: 300;
    font-style: normal;
}
@import 'blocks/fonts';

@media (min-width: 1200px){
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 960px;
    }
}
@media (min-width: 1441px){
    .container, .container-lg, .container-md, .container-sm, .container-xl {
        max-width: 1140px;
    }
}

body{
    font-family: 'Futura PT Book', sans-serif;
    font-weight: normal;
    font-style: normal;
    outline: none;
    background: #fcfcfc;
}
button.btn{
    background:$color_blue;
    padding: 0.8em 1.8em;
    font-size: 22px;
    @include font-bold;
    line-height: 120%;
    
    border-radius: 50px;
    outline: none;
    
    @include transition;
    a{
        color: #fff;
        text-decoration: none;
        @include transition;
    }
    @media screen and (max-width:1600px){
        font-size:20px;
        // padding: 15px 37px;
    }
    @media screen and (max-width:1440px){
        font-size:16.5px;
        // padding: 15px 37px;
    }
    @media screen and (max-width:1024px){
        font-size:16px;
        // padding: 15px 37px;
    }
    @media screen and (max-width:768px){
        font-size:18px;
        padding: 0.6em 1.6em;
    }
    @media screen and (max-width:320px){
        font-size:14px;
        padding: 0.8em 1.3em;
    }
        
}
button.btn.yellow{
    background: $color_yellow;
}
button.btn:hover{
    background: $color_yellow;
    a{
       color:#000; 
    }
    
}
button.btn.yellow:hover{
    background: $color_yellow_pressed;
}
button.btn:active{
    background: $color_yellow_pressed;
    a{
        color:#000; 
     }
}

.swiper-button-next, .swiper-button-prev,.swiper-pagination{
    position: static;
    margin-top: 0;
    height: auto;
    width: auto;
    margin: 0 10px;
}
.swiper-pagination-fraction{
    width: auto;
    color: #000;
    font-weight: 600;
}
.swiper-pagination-current{
    color: $color_blue;
}
.swiper-button-next,
.swiper-button-prev{
    outline: none;
    pointer-events: unset !important;
    opacity: 1 !important;

}
.swiper-button-prev:after,
.swiper-button-prev-gallery:after,
.swiper-button-next:after,
.swiper-button-next-gallery:after,
.slick-arrow::before{
    content: '';
    width: 9px;
    height: 15px;
    background-image: url('../img/arrow.png');
    background-repeat: no-repeat;
    background-size: cover;
    @include transition;
    display: block;
}
.slider_navigation{
    display: flex;
    align-items: center;
    // justify-content: center;
    margin-top: 64px;
    @media screen and (max-width: 1440px){
        margin-top: 0px;
    }
    @media screen and (max-width: 768px){
        margin-top: 0px;
    }
}
.cases .slider_navigation{
    @media screen and (max-width: 1440px){
        margin-top: -50px;
    }
    @media screen and (max-width: 768px){
        margin-top: 20px;
    }
}

.swiper-button-next:hover::after,
.swiper-button-next-gallery:hover::after,
.swiper-button-prev:hover::after,
.swiper-button-prev-gallery:hover::after,
.slick-next:hover::before{
    background-image: url('../img/arrow_hover.png');
}
.swiper-button-next:active::after,
.swiper-button-next-gallery:active::after,
.swiper-button-prev:active::after,
.swiper-button-prev-gallery:active::after,
.slick-prev:active::before{
    background-image: url('../img/arrow_pressed.png'); 
}
.swiper-button-prev:after,
.swiper-button-prev-gallery:after,
.slick-prev:before{
    transform: rotate(180deg);
}
.slick-next, .slick-prev{
    width: auto;
    height: auto;
}
.mobile{
    display: none !important;
    @media screen and (max-width:768px) {
        display: block !important;
    }
}
.hide{
    display: block !important;
    @media screen and (max-width:768px) {
        display: none !important;
    }
}
.hide-flex{
    display: flex !important;
    @media screen and (max-width:768px) {
        display: none !important;
    }
}
@import 'blocks/header';

@import 'blocks/main';

@import 'blocks/footer';

@import 'blocks/popups'




