header.page{
    background: #fff;
    position: relative;
    z-index: 10;
    height: auto;
    background-image: none;
    .container{
        height: auto;
    }

    .header__menu_container{
        padding-bottom: 25px;
        padding-top: 32px;
        
    }
    
}
.header_page__content{
    color: #000;
    position: relative;
    min-width: 60vw;
    .pat{
        position: absolute;
        top: 70px;
        left: 0;
    }
    h1{
        margin-top: 70px;
        margin-bottom: 84px;
        font-size: 50px;
        span{
            font-weight: 600;
        }
    }
    .list{
        display: flex;
        justify-content: space-between;
        span{
            font-size: 20px;
            @include font-bold;
        }
        ul{
            padding-left: 0;
            li{
                position: relative;
                list-style: none;
                font-size: 17px;
                line-height: 130%;
                margin-bottom: 10px;
                padding-right: 50px;
            }
            li:before{
                content: '';
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                left: -40px;
                width: 20px;
                height: 20px;
                background-image: url('../img/list_style_plus.png');
            }
        }
    }
    img.preview_header{
        position: absolute;
        right: 0;
        top: -80px;
        z-index: 9;
        max-width: 55%;
        @media screen and (max-width: 1600px) {
            top: -30px;
            max-width: 58%;
        }
    }
    @media screen and (max-width:1440px){

            h1{
                font-size: 38px;
            }
            .list{
                span{
                    font-size: 15px;
                }
                ul{
                    li{
                        font-size: 18px;
                    }
                }
            }

    }
    @media screen and (max-width:1024px){
        h1{
            font-size: 40px;
            margin-top: 30px;
            margin-bottom: 40px;
        }
        .pat{
            display: none;
        }
        .list{
            span{
                font-size: 18px;
            }
            ul{
                li{
                    font-size: 13px;
                }
            }
        }
    }
    @media screen and (max-width:768px){

        // padding-bottom: 16px;
        // padding-top: 32px;
        .row{
            flex-wrap: wrap-reverse;
            justify-content: flex-end;
        }
        img.preview_header{
            position: static;
            margin-top: 0px;
            max-width:90%;
        }
        h1{
            font-size: 24px;
            text-align: center;
        }
        .list{
            display: block;
            padding-left: 20px;
            span{
                margin-bottom: 20px;
                display: block;
            }
            ul{
                padding-left: 40px;
            }
        }
    }
    @media screen and (max-width:320px){

        // padding-bottom: 16px;
        // padding-top: 32px;
        .row{
            flex-wrap: wrap-reverse;
            justify-content: flex-end;
        }
        img.preview_header{
            position: static;
            margin-top: 0px;
            max-width:90%;
        }
        h1{
            font-size: 20px;
            text-align: center;
        }
        .list{
            display: block;
            padding-left: 20px;
            span{
                margin-bottom: 20px;
                display: block;
            }
            ul{
                padding-left: 40px;
            }
        }
    }
}