.popup_container{
    position: fixed;
    z-index: 99999;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    @include transition_fade;
    
    
    background: rgba(0,0,0,0.5);
    .popup_wrapper{
        min-height: 600px;
        position: relative;
        display: flex;
        align-items: center;
        background: #fff;
        width: 70%;
        text-align: center;
        padding: 65px 0 ;
        color: #485B69;
        h1{
            font-size: 36px;
            font-weight: 600;
        }
        p{
            font-size: 20px;
        }
        .popup__content{
            padding: 0 20%;
            width: 100%;
        }
        .close{
            position: absolute;
            right: 65px;
            top: 65px;
        }
        form{
            input{
                width: 100%;
                border: none;
                border-bottom:  1px solid #DADADA;
                padding: 10px;
                margin-bottom: 20px;
                font-size: 18px;
            }
            
        }
        button.btn{
            background: $color_yellow;
            font-family: 'Jura', sans-serif;
            font-weight: normal;
            opacity: .8;
            margin-top: 30px;
        }
    }
    @media screen and (max-width: 768px){
        .popup_wrapper{
            width: 100%;
            // padding: 0 6%;
            h1{
                font-size: 24px;
            }
            p{
                font-size: 14px;
            }
            .close{
                right: 30px;
                top: 30px;
            }
            .popup__content{
                padding: 0 6%;
            }

        }
    }
}
.popup_container.show{
    @include transition_show;
}
.popup_container#policy{
    
    height: 100%;
    align-items: unset;
    .popup_wrapper{
        overflow: auto;
        height: auto;
        align-items: unset;
        text-align: left;
        
        .popup__content{
            
            h1{
                font-size: 40px;
                margin-bottom: 30px;
            }
            h2{
                font-size: 24px;
                margin-bottom: 20px;
                font-weight: 600;
            }
            p{
                font-size: 18px;
            }
            
        }
    }
}