header, .header_fixed{
    width: 100%;
    height: 100vh;
    background-color: #FCFCFC;
    background-image: url('../img/header_bg.png');
    background-repeat: no-repeat;
    background-position: top left;
     background-size: auto 100%;
     position: relative;
     overflow: hidden;
     .container{
         height: 100%;
         padding-right: 30px;
         padding-left: 30px;
     }

     .header__menu_container{
         padding-top: 44px;
         display: flex;
         align-items: center;
         justify-content: space-between;

         .logo{
             max-width: 184px;
         }
         .menu{
             display: flex;
             align-items: center;
             nav{
                 margin-right: 80px;
             }
             nav li{
                 display: inline-block;
                 font-size: 18px;
                 line-height: 150%;
                 margin-left: 24px;
                 color:#000;
                 position: relative;
                 a{
                     color: inherit;
                     text-decoration: none;
                 }
             }
             nav li::before{
                 content: ' ';
                 width: 32px;
                 height: 5px;
                 background: $color_yellow;
                 position:  absolute;
                 bottom: -10px;
                 left: 50%;
                 border-radius: 4px;
                 transform: translateX(-16px);
                 @include transition_fade;
             }
             nav li:hover::before{
                 bottom: -16px;
                 @include transition_show;
             }
             .number{
                 margin-right: 22px;
                 a{
                    @include font-bold;
                    color: #000;
                    font-size: 19px;
                    text-decoration: none;
                 }
             }
             .socials{
                 span{
                     font-size: 24px;
                     color:$color_blue;
                     display: inline-block;
                     margin-left: 14px;
                     
                     
                 }
                 span::before{
                   @include transition;  
                 }
                 span:hover::before{
                    color: $color_yellow;
                    
                }
                 
             }
         }
     }
     .header__content{
        //  position:  absolute;
