.overlay-button {
//   position: absolute;
//   right: 2em;
//   top: 3em;
position: relative;
z-index: 9999;
  padding: 30px 11px;
  display: none;

  cursor: pointer;
  user-select: none;
  span {
      height: 3px;
      width: 30px;
      border-radius: 2px;
      background-color: #000;
      position: relative;
      display: block;
      transition: all .2s ease-in-out;
      &:before {
        top: -8px;
        visibility: visible;
      }
      &:after {
        top: 8px;
      }
      &:before, &:after {
          height: 3px;
          width: 30px;
          border-radius: 2px;
          background-color: #000;
          position: absolute;
          content: "";
          transition: all .2s ease-in-out;
      }
    }
    &:hover span, &:hover span:before, &:hover span:after {
      background: #333332;
    }
    @media screen and (max-width: 768px) {
        display: block;
    }
}



// input[type=checkbox]:checked ~ #overlay {
//   visibility: visible;
// }

.overlay-button.active {
  &:hover span, span{
    background: transparent;
  }
  span {
    &:before {
      transform: rotate(45deg) translate(6px, 6px);
      opacity: 1;
      background: #fff;
    }
    &:after {
      transform: rotate(-45deg) translate(6px, -6px);
      background: #fff;
    }
  }
}

#overlay {
  height: 100vh;
  width: 100vw;
  background: $color_blue;
  visibility: hidden;
  opacity: 0;
  position: fixed;
  flex-direction: column;
  top: 0; left: 0;
  z-index: 9998;
  display: flex;
  justify-content: center;
  align-items: center;
//   display: none;
//   &.active {
//     visibility: visible;
//   }
  ul {
    display: flex;


    flex-direction: column;
    text-align: center;
    padding-left: 0;
    list-style-type: none;
    li {
      padding: 1em;
      a {
        color: white;
        text-decoration: none;
        font-size: 28px;
        &:hover {
          color: #333332;
        }
      }
    }
  }
  .socials{
      display: block;
      margin-top: 20px;
      span:not(.end){
        margin-right: 20px;
    }
    span::before{
        color: #fff;
    }
  }
  a {
    color: white;
    text-decoration: none;
    font-size: 28px;
    &:hover {
      color: #333332;
    }
  }
  
  
}
#overlay.show{
    visibility: visible;
    opacity: 1;
    transition: .5s all easy;
  }

  .header_fixed{
    box-shadow: 20px 5px 31px -14px rgba($color: #000000, $alpha: 0.5);
      position: fixed;
      height: auto;
      top: 0;
      left: 0;
      width: 100%;
      @include transition_fade;
      z-index: 9998;
      background: #fff;
      .container{
          height: auto;
      }
      .header__menu_container{
          padding: 15px 0;
          .logo{
             max-width: 140px;
             img{
                 width: 100%;
             }
         }
      }
      
      
  }
  .header_fixed.show{
      @include transition_show;
  }
  .header_fixed.show.open{
      background: transparent;
      box-shadow: none;
      .header__menu_container{
          .logo{
          display: none !important;
      }
      }
      
  }
